*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Courier New', Courier, monospace,
    'Times New Roman', Times, serif,
    Arial, Helvetica, sans-serif,
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
body{
    background-image: linear-gradient(
        90deg,
        #454 0%,
        #331 90%
    );
    font-size: 14px;
    font-weight: bold;
    color: rgb(100,255,0);
    text-align: center;
}
.app-wrapper{
    box-shadow: 0 0 5px 2px #000,
    0 0 20px 5px #000;
    width: 50%;
    margin: 20px auto;
    padding: 50px 40px;
    border-radius: 10px;
    background: linear-gradient(
        90deg,
        #564321,
        #967788
    );
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: flex;
    min-height: 100vh;
}
.title{
    color: #000;
    font-size: 30px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 20px 0 50px 0;
    text-shadow: #030 0 1px 4px;
}
.form-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.name,.email,.password{
    display: flex;
    flex-direction: column;
    color: #000;
    font-size: 16px;
    text-shadow: whitesmoke 0 1px;
    text-align: left;
    width: 50%;
    margin: 20px;
}
.label{
    margin: 10px 0;
}
.input{
    background: none;
    border: none;
    border-bottom: 2px yellow solid;
    width: 120%;
    padding: 5px 10px;
    color: #000;
    text-shadow: #333 2 1px;
}
.submit{
    padding: 10px 40px;
    font-size: 20px;
    font-weight: bold;
    background: linear-gradient(
        50deg,
        #680 0%,
        #230 100%
    );
    border-radius: 25px;
    margin: 40px 0 ;
    box-shadow: #222 0 0 10px 5px,
    #333 0 3px 20px 15px;
}
button:active{
    transform: translateY(2px);
}
.error{
    box-shadow: 0 0 2px 1px red ;
    padding: 5px 10px;
    border-radius: 5px;
    background: linear-gradient(
        190deg,
        rgb(200,150,0) 0%,
        rgb(255,100,0) 100%
    );
    margin: 10px auto;
}
.form-success{
    color: rgb(100, 0, 10);
    text-shadow: #030 1px 0 ;

}